.title {
    font-size: 18px;
}

.material {
    font-size: 16px;
}

.card {
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 30px;
    padding: 30px;
    text-align: left;
    border-radius: 10px;
    font-size: 16px;
    /* font-weight: 200; */
    letter-spacing: 1px;
    padding: 13px 20px 13px;
    outline: 0;
    border: 1px solid black;
    cursor: pointer;
    position: relative;
    background-color: rgba(0, 0, 0, 0);
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.card:after {
    content: "";
    background-color:rgb(236, 236, 236);
    width: 100%;
    z-index: -1;
    position: absolute;
    height: 100%;
    top: 10px;
    left: 10px;
    transition: 0.2s;
}

.card:hover:after {
    top: 0px;
    left: 0px;
}

@media (min-width: 768px) {
    .card {
        padding: 13px 50px 13px;
    }
}